@media (min-width: 767px) /* @grid-float-breakpoint -1 */
{
    .navbar-fixed-top
    {
    position: absolute;
    top: auto;
    }
}
.navbar-custom .navbar-brand{
	color:#fff;
}
header{
	box-shadow: inset 0 0 0 1000px rgba(26, 0, 45, 0.59);
}
#rendering-web{
	background-color:#5b18ba;
	color:#fff;
}

.intro-text .section-subheading{
	width: 50%;
    margin: auto;
    padding: 20px 0;
    font-size:18px;
}
.intro-lead-in{
	margin-bottom:0;
}
.btn-learn-more{
	background-color:#222222;
}
#rendering-web .btn-learn-more{
	border : 1px solid #000;
	color:#fff;
}

btn:active, .btn:hover{
	background-image: none;
    outline: 0;
    -webkit-box-shadow: inset 0 3px 5px rgba(0, 0, 0, .5);
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, .5);
}

/*
header .btn-learn-more:hover, .btn-xl:hover, header .btn-learn-more.focus{
	border : 1px solid #5b18ba;
	background-color:#5b18ba;
	color:#fff;
}
*/
header .btn-learn-more{
	border : 1px solid #5b18ba;
}

.btn-learn-more:hover{
	color:#fff;
}

#services{
	background-color:#333333;	
} 
#services h2{
	color:#fff;
}
#services i{
	color:#5b18ba;
}
#services .service-heading{
	color:#fff;
}

#team h3{
	color:#5b18ba;
}
#team h4{
	color:#8d08a2;
}
.team-member img{
	width: 200px;
    height: 200px;
}
.clients{
	background-color:#5b18ba;
}
.list-inline li a img{
	width:100px;

}
.clients .list-inline li{
	vertical-align: middle;
    padding: 0 50px;	
}

#contact .service-heading, footer .service-heading{
	color:#fff;
}
#contact .social-buttons li a{
	text-align:center;
}

#contact .text-muted , #services .text-muted{
	color: #ccc;
}
#contact input, #contact textarea{
	color:#333;
	font-weight: bold;
}
.btn-submit, .btn-submit:hover, .btn-submit:focus{
	border : 1px solid #5b18ba;
	background-color:#5b18ba;
	color:#fff;
}
#team .text-muted, h3 .text-muted{
	color:#333;
}
.section-heading, h3,h2{
	text-transform:none;
}
.section-subheading{
	font-family: inherit;
    font-style: normal;
    line-height:1.5;
}
.intro-lead-in{
	font-style:normal;
}
footer{
	background-color:#222;
}
.border-top{
	border-top:1px solid #ccc;
}
.copyright-text{
	margin:10px 0 0;
	padding:10px 0 0;
}
.portfolio-modal{
	color:#333;
}

::-webkit-input-placeholder {
	color: #999 !important;
	text-transform:none !important;
}
 
:-moz-placeholder { /* Firefox 18- */
	color: #999 !important;  
	text-transform:none !important; 
}
 
::-moz-placeholder {  /* Firefox 19+ */
	color: #999 !important;  
	text-transform:none !important; 
}
 
:-ms-input-placeholder {  
	color: #999 !important;  
	text-transform:none !important; 
}
ul.social-buttons li a{
	border:1px solid #5b18ba;
}
ul.social-buttons li a:hover{
	background-color:#fff;
	color:#5b18ba;
}
.service-icon{
	font-size:64px
}
.service-icon:hover{ 
	color:#fff !important;
}
.social-buttons, .service-icon,img.img-responsive.img-circle:hover{ 
	-webkit-transition: all .2s linear; 
	-o-transition: all .2s linear; 
	-moz-transition: all .2s linear; 
	transition: all .2s linear; 
}
img.img-responsive.img-circle:hover {
    border-color: #5b18ba;
}
